:host {
  display: block;
  width: 100%;
  min-height: 100vh;
  background: var(--bg-primary);
}

.customer-profile-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
  min-height: calc(100vh - 200px);
  background: var(--bg-primary);
}

.profile-header {
  margin-bottom: 2rem;
  
  h1 {
    font-size: 2rem;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0 0 0.5rem 0;
  }
  
  .subtitle {
    font-size: 0.95rem;
    color: var(--text-secondary);
    margin: 0;
  }
}

.profile-content {
  background: var(--bg-secondary);
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  padding: 2rem;
  border: 1px solid var(--border-color);
  transition: background 0.3s ease, border-color 0.3s ease;
}

:host-context(.dark-theme) .profile-content,
:host-context([data-theme="dark"]) .profile-content {
  background: var(--bg-secondary);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  border: 1px solid var(--border-color);
}

.no-user {
  text-align: center;
  padding: 3rem;
  color: var(--text-secondary);
  
  mat-icon {
    font-size: 4rem;
    width: 4rem;
    height: 4rem;
    margin-bottom: 1rem;
    opacity: 0.5;
    color: var(--text-secondary);
  }
  
  p {
    font-size: 1rem;
    margin: 0.5rem 0;
    color: var(--text-primary);
    
    &.hint {
      font-size: 0.9rem;
      color: var(--text-secondary);
      opacity: 0.8;
    }
  }
}

/* 모바일 반응형 */
@media (max-width: 768px) {
  .customer-profile-container {
    padding: 1rem;
  }
  
  .profile-header h1 {
    font-size: 1.5rem;
  }
  
  .profile-content {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .customer-profile-container {
    padding: 0.75rem;
  }
  
  .profile-header {
    margin-bottom: 1.5rem;
  }
  
  .profile-content {
    padding: 1rem;
    border-radius: 8px;
  }
}
